@import '../../styles/base';

.form {
    @extend .page;

    h2 {
        margin: 0; }

    p {
        margin-bottom: 0; }

    img {
        width: 223px;
        margin: auto; }


    form {
        min-width: 120px;
        display: flex;
        flex-direction: column;
        flex: 1;
        justify-content: flex-start; }

    #form_2, #form_1 {
        flex: 2; }

    .formRow {
        margin-bottom: 2em; }

    .btn-back {
        text-transform: uppercase;
        letter-spacing: 0.5px;
        margin-top: -2em;
        text-decoration: none;
        color: black; }

    .two-col-input {
        display: flex;
        align-items: baseline; }

    .two-col-input > div:first-child {
        margin-right: 0.5em; }

    .two-col-input > div:nth-child(2) {
        margin-left: 0.5em; } }
